<template>
  <div class="EditPrivateTaste-container">
    <headerUsually
      btnShow="true"
      titleText=""
      url
      :isComponents="isComponents"
      @emitGoback="gobackFun()"
    ></headerUsually>
    <div class="content-taste">
      <div class="wrapper" ref="wrapper">
        <ul class="container">
          <li
            class="right-item"
            :class="item.selected?'right-item-active':''"
            @click="rightClickFun(item,index)"
            v-for="(item, index) in receiveItem.pluCondgplist"
            :key="index"
          >
            <span class="text" v-if="item.price>0">{{item.descript}} (+{{item.price}})</span>
            <span class="text" v-else>{{item.descript}}</span>
            <span class="iconfont icon-xuanzhong" v-if="item.selected==true"></span>
          </li>
          <!-- <li class="right-item right-item-active">
            <span class="text">11111</span>
            <span class="iconfont icon-xuanzhong"></span>
          </li>-->
        </ul>
      </div>
    </div>
    <div class="btn" @click="okFun()"></div>
  </div>
</template>
<script>
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import BScroll from "better-scroll";
export default {
  components: {
    headerUsually
  },
  props: {
    selectedItem: Object,
    from: String
  },
  data() {
    return {
      isComponents: true,
      receiveItem: {}
    };
  },
  created() {
    //console.log("from:", this.from);
    //console.log("selectedItem:", this.selectedItem);
    this.receiveItem = _.cloneDeep(this.selectedItem);
    this.receiveItem.pluCondgplist.forEach(ele => {
      this.$set(ele, "selected", false);
    });
    this.receiveItem.dishSPrice = 0;
  },
  mounted() {
    this.$nextTick(() => {
      let scroll = new BScroll(this.$refs.wrapper, { click: true });
    });
  },
  methods: {
    gobackFun() {
      this.$emit("emitHiddenPrivateTaste");
    },
    rightClickFun(item, index) {
      item.selected = !item.selected;
    },
    okFun() {
      let dishSPrice = 0;
      this.receiveItem.pluCondgplist.forEach(ele => {
        if (ele.selected) {
          dishSPrice += Number.parseFloat(ele.price);
        }
      });
      this.receiveItem.dishSPrice = dishSPrice;
      if (this.from == "orderDish") {
        this.receiveItem.number++;
      }
      this.$emit("emitHiddenPrivateTaste", this.receiveItem);
    }
  }
};
</script>
<style lang="less" scoped>
.EditPrivateTaste-container {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9;
  top: 0;
  background-color: #f6f8f9;
  .content-taste {
    background-color: #f6f8f9;
    position: absolute;
    top: 46px;
    bottom: 50px;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    .container {
      width: 100vw;
      .right-item {
        padding-left: 18px;
        padding-right: 15px;
        height: 55px;
        line-height: 55px;
        font-size: 14px;
        color: #666666;
        border-bottom: 1px solid #ddd;
        display: flex;
        align-content: center;
        justify-content: space-between;
        background-color: #fff;
        font-size: 14px;
      }
      .right-item-active {
        color: #c4ad68;
        .iconfont {
          font-size: 18px;
          color: #c4ad68;
        }
      }
    }
  }
  .btn {
    position: fixed;
    bottom: 0;
    width: 100%;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 12px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.5);
  }
}
</style>



