<template>
  <div class="temporaryDishList">
    <headerUsually
      btnShow="true"
      titleText=""
      url
      :isComponents="isComponents"
      @emitGoback="gobackFun()"
    ></headerUsually>
    <div class="container-main">
      <div class="wrapper" ref="wrapper">
        <ul class="container">
          <li
            class="item"
            v-for="(item, index) in receiveItem.tempDishList"
            :key="index"
            @click="goTempDishModifyFun(item,index)"
          >
            <!-- <p class="item-time">
              
              <span class="date">2019/10/10</span> 13:25:56
            </p>-->
            <div class="item-content">
              <div class="num">{{index}}</div>
              <div class="menu-content">
                <div class="menu-name">{{item.name}}</div>
                <!-- <div class="ingredients">
                  <span></span>
                  <span></span>
                </div>-->
              </div>
              <div class="quantity">
                <span>X {{item.quantity}}</span>
                <span class="iconfont icon-ren" v-if="waiter==item.waiter"></span>
              </div>

              <div class="price">{{item.price}}</div>
              <i class="iconfont icon-jiantou1"></i>
            </div>
          </li>
          <!-- <li class="item">
            <p class="item-time">
              
              <span class="date">2019/10/10</span> 13:25:56
            </p>
            <div class="item-content">
              <div class="num">99</div>
              <div class="menu-content">
                <div class="menu-name"></div>
              </div>
              <div class="quantity">X 1</div>
              <div class="price">300.00</div>
              <i class="iconfont icon-jiantou1"></i>
            </div>
          </li>-->
        </ul>
      </div>
    </div>
    <tempDishModify
      v-if="showAddTempDish"
      :selectedItem="receiveItem"
      :selectedIndex="selectedIndex"
      @emitHiddenAddTempDish="hiddenEditAddTempDishFun($event)"
    ></tempDishModify>
  </div>
</template>
<script>
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import tempDishModify from "@/components/common/mobileOrdering/tempDishModify.vue";
import BScroll from "better-scroll";
export default {
  components: {
    headerUsually,
    tempDishModify
  },
  props: {
    selectedItem: Object
  },
  data() {
    return {
      isComponents: true,
      waiter: "",
      showAddTempDish: false,
      selectedIndex: -1
    };
  },
  created() {
    console.log("selectedItem:", this.selectedItem);
    this.receiveItem = _.cloneDeep(this.selectedItem);
  },
  mounted() {
    let dcgateway = localStorage.getItem("dcgateway")
      ? JSON.parse(localStorage.getItem("dcgateway"))
      : {};
    this.waiter = dcgateway.userInfo.userName;
    let scrollLeft = new BScroll(this.$refs.wrapper, {
      click: true
    });
    // this.$nextTick(() => {
    //   let scrollLeft = new BScroll(this.$refs.wrapper, {});
    // });
  },
  methods: {
    gobackFun() {
      this.$emit("emitHiddenTempDishList", this.receiveItem);
    },
    goTempDishModifyFun(item, index) {
      this.showAddTempDish = true;
      this.selectedIndex = index;
    },
    hiddenEditAddTempDishFun(item) {
      if (item) {
        this.receiveItem = item;
        let tempDishList = [];
        this.receiveItem.tempDishList.forEach(element => {
          if (element.quantity > 0) {
            tempDishList.push(element);
          }
        });
        this.receiveItem.tempDishList = tempDishList;
      }
      this.showAddTempDish = false;
    }
  }
};
</script>
<style lang="less" scoped>
.temporaryDishList {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9;
  top: 46px;
  background-color: #f6f8f9;
  .container-main {
    .wrapper {
      position: absolute;
      top: 0px;
      bottom: 0px;
      width: 100%;
      // background-color: red;
      z-index: 0;
      .item {
        margin: 0 18px;
        padding: 10px 0;
        text-align: left;
        border-bottom: 1px dashed #ddd;
        .item-time {
          font-size: 12px;
          color: #000000;
          .date {
            margin: 0 5px;
          }
        }
        .item-content {
          margin-top: 3px;
          color: #000000;
          display: flex;
          align-items: center;
          .num {
            width: 25px;
          }
          .menu-content {
            flex: 1;
            text-align: left;
            .menu-name {
              font-size: 14px;
            }
            .ingredients {
              margin-top: -5px;
              font-size: 12px;
              color: #dd0a09;
            }
          }
          .quantity {
            width: 50px;
            margin-right: 30px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .icon-ren {
              text-align: right;
              color: #c4ad68;
            }
          }
          .price {
            width: 50px;
          }
          .icon-jiantou1 {
            color: #a8acb8;
          }
        }
      }
    }
  }
}
</style>



