<template>
  <div class="container-orderDish">
    <div class="content-header">
      <div class="content-goback" @click="goback()">
        <div class="iconfont icon-jiantouzuo goback-icon"></div>
        <span class="goback-text"></span>
      </div>
      <search tips="" @emitSearchFun="searchFun($event)" @emitClearFun="clearFun()" />
    </div>
    <div class="container-main">
      <div class="dis-container" v-if="leftArr.length>0">
        <div class="left" ref="wrapperLeft">
          <ul class="container">
            <li
              class="left-item"
              :class="leftIndex==index?'left-item-active':''"
              v-for="(item, index) in leftArr"
              :key="index"
              @click="clickLeftItemFun(item,index)"
            >
              <span class="text">{{item.sortDescript}}</span>
              <span class="tips" v-if="item.number>0">{{item.number|deleteZero}}</span>
            </li>
            <!-- <li class="left-item">
            <span class="text"></span>
            <span class="tips">22</span>
            </li>-->
          </ul>
        </div>
        <div class="right" ref="wrapperRight">
          <ul class="container" v-if="rightArr.length>0">
            <li class="right-item" v-for="(item, index) in rightArr" :key="index">
              <div class="dish">
                <div class="dish-name">{{item.pluDescript}}</div>
                <div class="dish-price">{{item.pluPrice}}/{{item.pluUnit}}</div>
              </div>
              <div class="quantity-content">
                <i class="iconfont icon-jian" v-if="item.number>0" @click="cutBackFun(item, index)"></i>
                <!-- <span class="quantity" v-if="item.number>0">{{item.number}}</span> -->
                <input
                  class="quantity"
                  type="number"
                  v-if="item.number>0"
                  v-model="item.number"
                  @input="inputFun(item,index)"
                  @focus="focusFun(item)"
                  @blur="blurFun(item)"
                />

                <i class="iconfont icon-jia2" @click="addFun(item, index)"></i>
              </div>
            </li>
          </ul>
          <div class="no-list" v-else>
            <noResult colors="rgba(57, 60, 65, 0.8)" text=""></noResult>
          </div>
        </div>
      </div>
      <div class="no-list" v-else>
        <noResult colors="rgba(57, 60, 65, 0.8)" text="!"></noResult>
      </div>
    </div>
    <div class="container-footer">
      <div class="shoppingCart-content" @click="gotoCartFun()">
        <div class="iconfont icon-gouwucheman"></div>
        <div class="quantity">{{cartNumber|deleteZero}}</div>
      </div>
      <div class="totalPrice-content">
        <span class="unit"></span>
        {{totalPrice|toFixed}}
      </div>
      <div class="ok-btn" @click="okFun()"></div>
    </div>
    <EditSeafood
      v-if="showEditSeafoodCom"
      :selectedItem="selectedItem"
      @emitHiddenEditSeafood="hiddenEditSeafoodFun($event)"
    ></EditSeafood>
    <EditPrivateTaste
      v-if="showEditPrivateTasteCom"
      :selectedItem="selectedItem"
      from="orderDish"
      @emitHiddenPrivateTaste="hiddenEditPrivateTasteFun($event)"
    ></EditPrivateTaste>
    <tempDishModify
      v-if="showAddTempDish"
      :selectedItem="selectedItem"
      @emitHiddenAddTempDish="hiddenEditAddTempDishFun($event)"
    ></tempDishModify>
    <tempDishList
      v-if="showTempDishList"
      :selectedItem="selectedItem"
      @emitHiddenTempDishList="hiddenEditTempDishListFun($event)"
    ></tempDishList>
  </div>
</template>
<script>
import ajaxGet, { ajaxPost } from "@/conf/ajaxGet";
import { Toast, MessageBox } from "mint-ui";
import search from "@/components/common/mobileOrdering/search.vue";
import EditSeafood from "@/components/common/mobileOrdering/EditSeafood.vue";
import EditPrivateTaste from "@/components/common/mobileOrdering/EditPrivateTaste.vue";
import tempDishModify from "@/components/common/mobileOrdering/tempDishModify.vue";
import tempDishList from "@/components/common/mobileOrdering/tempDishList.vue";
import noResult from "@/components/common/mobileOrdering/noResult.vue";

import BScroll from "better-scroll";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    search,
    EditSeafood,
    EditPrivateTaste,
    tempDishModify,
    tempDishList,
    noResult
  },
  data() {
    return {
      showEditSeafoodCom: false,
      showEditPrivateTasteCom: false,
      showAddTempDish: false,
      showTempDishList: false,
      leftArr: [],
      rightArr: [],
      leftIndex: 0,
      scrollRight: "",
      cartNumber: 0,
      totalPrice: 0,
      selectedItem: {},
      //
      originalNumber: 0,
      max100: 100
    };
  },
  computed: {
    ...mapState({
      salesPoint: state => state.mobileOrdering.salesPoint,
      shoppingCart: state => state.mobileOrdering.shoppingCart,
      selectedTableInfo: state => state.mobileOrdering.selectedTableInfo,
      dishInfo: state => state.mobileOrdering.dishInfo
    })
  },
  created() {
    //console.log("selectedTableInfo:", this.selectedTableInfo);
    //console.log("this.shoppingCart:", this.shoppingCart);

    if (this.selectedTableInfo.pcCode) {
      this.dishInfo.forEach(element => {
        if (element.pccode == this.selectedTableInfo.pcCode) {
          this.leftArr = element.sortPluData;
          this.initArr();
        }
      });
    } else {
      this.dishInfo.forEach((element, index) => {
        this.leftArr.push(...element.sortPluData);
      });
      this.initArr();
    }
  },
  mounted() {},
  methods: {
    initArr() {
      this.shoppingCart.forEach(shopEle => {
        let pluFlagIndex = shopEle.pluFlag.indexOf("1");
        if (pluFlagIndex == 1) {
          let SPrice = Number.parseFloat(shopEle.pluPrice);
          shopEle.posCondstDtoList.forEach(ele => {
            ele.posConddtlList.forEach(opt => {
              if (opt.selected) {
                SPrice += Number.parseFloat(opt.price);
              }
            });
          });
          this.totalPrice =
            Number(this.totalPrice) +
            SPrice * Number.parseFloat(shopEle.number);
        } else if (pluFlagIndex == 2) {
          let SPrice = 0;
          shopEle.tempDishList.forEach(ele => {
            SPrice +=
              Number.parseFloat(ele.price) * Number.parseFloat(ele.quantity);
          });
          this.totalPrice = Number(this.totalPrice) + SPrice;
        } else {
          let SPrice = Number.parseFloat(shopEle.pluPrice);
          shopEle.pluCondgplist.forEach(ele => {
            if (ele.selected) {
              SPrice += Number.parseFloat(ele.price);
            }
          });
          this.totalPrice =
            Number(this.totalPrice) +
            SPrice * Number.parseFloat(shopEle.number);
        }
      });
      this.leftArr.forEach((ele, index) => {
        let leftNumber = 0;
        this.shoppingCart.forEach(shopEle => {
          if (
            ele.pccode == shopEle.pccode &&
            ele.sortCode == shopEle.sortCode
          ) {
            leftNumber = (
              Number(leftNumber) + Number.parseFloat(shopEle.number)
            ).toFixed(2);
            this.cartNumber = (
              Number(this.cartNumber) + Number.parseFloat(shopEle.number)
            ).toFixed(2);
          }
        });
        this.$set(ele, "number", leftNumber);
        ele.pluDtos.forEach((opt, i) => {
          let isMatch = false;
          this.shoppingCart.forEach(shopEle => {
            if (
              opt.pccode == shopEle.pccode &&
              opt.sortCode == shopEle.sortCode &&
              opt.pluCode == shopEle.pluCode
            ) {
              Object.assign(opt, shopEle);
              isMatch = true;
            }
          });
          if (!isMatch) {
            this.$set(opt, "parentsIndex", index);
            this.$set(opt, "index", i);
            this.$set(opt, "number", 0);
            this.$set(opt, "dishSPrice", 0);
            this.$set(opt, "remarks", "");
            if (opt.pluFlag.indexOf("1") == 2) {
              this.$set(opt, "tempDishList", []);
            }
          }
        });
      });

      this.rightArr =
        this.leftArr[0] && this.leftArr[0].pluDtos
          ? this.leftArr[0].pluDtos
          : [];
      //console.log("this.rightArr:", this.rightArr);
      this.$nextTick(() => {
        if (this.leftArr.length > 0) {
          let scrollLeft = new BScroll(this.$refs.wrapperLeft, { click: true });
        }
        if (this.rightArr.length > 0) {
          this.scrollRight = new BScroll(this.$refs.wrapperRight, {
            click: true
          });
        }
      });
    },
    searchFun(name) {
      this.rightArr = [];
      let searchResultAll = [];
      this.leftArr.forEach((ele, index) => {
        ele.pluDtos.forEach((opt, i) => {
          if (opt.pluDescript.indexOf(name) > -1) {
            searchResultAll.push(opt);
          }
          if (opt.helpCode.indexOf(name) > -1) {
            searchResultAll.push(opt);
          }
        });
      });
      // console.log("chognfu:", searchResultAll);
      for (let i = searchResultAll.length - 1; i >= 0; i--) {
        let pd = false;
        this.rightArr.forEach(opt => {
          if (
            opt.pccode == searchResultAll[i].pccode &&
            opt.sortCode == searchResultAll[i].sortCode &&
            opt.pluCode == searchResultAll[i].pluCode
          ) {
            pd = true;
          }
        });
        if (!pd) {
          this.rightArr.push(searchResultAll[i]);
        }
      }
      console.log("rightArr:", this.rightArr);
    },
    clearFun() {
      this.rightArr = this.leftArr[this.leftIndex].pluDtos;
    },
    goback() {
      this.$router.push("/mobileOrdering/myOrder");
    },
    hiddenEditSeafoodFun(item) {
      this.showEditSeafoodCom = false;
      // console.log(item);
      if (item) {
        let previousItem = this.leftArr[item.parentsIndex].pluDtos[item.index];
        let changeNumber = item.number - previousItem.number;
        this.leftArr[item.parentsIndex].number = (
          Number(this.leftArr[item.parentsIndex].number) + changeNumber
        ).toFixed(2);
        this.cartNumber = (Number(this.cartNumber) + changeNumber).toFixed(2);

        //totalPrice
        let changePrice =
          (Number.parseFloat(item.dishSPrice) +
            Number.parseFloat(item.pluPrice)) *
            item.number -
          (Number.parseFloat(previousItem.dishSPrice) +
            Number.parseFloat(previousItem.pluPrice)) *
            previousItem.number;
        this.leftArr[item.parentsIndex].pluDtos[item.index] = item;
        this.totalPrice = Number(this.totalPrice) + changePrice;
      }
    },
    hiddenEditPrivateTasteFun(item) {
      this.showEditPrivateTasteCom = false;
      if (item) {
        this.leftArr[item.parentsIndex].number =
          Number(this.leftArr[item.parentsIndex].number) + 1;
        this.cartNumber = Number(this.cartNumber) + 1;
        //this.leftArr[item.parentsIndex].number++;
        //this.cartNumber++;
        let changePrice =
          Number.parseFloat(item.dishSPrice) + Number.parseFloat(item.pluPrice);
        this.leftArr[item.parentsIndex].pluDtos[item.index] = item;
        this.totalPrice = Number(this.totalPrice) + changePrice;
      }
    },
    hiddenEditAddTempDishFun(item) {
      //console.log("item:", item);
      if (item) {
        this.TempDishOperating(item);
      }
      this.showAddTempDish = false;
    },
    hiddenEditTempDishListFun(item) {
      this.showTempDishList = false;
      this.TempDishOperating(item);
    },
    TempDishOperating(item) {
      console.log(item);
      let previousItem = this.leftArr[item.parentsIndex].pluDtos[item.index];
      if (!previousItem.tempDishList) {
        previousItem.tempDishList = [];
      }
      let previousNumber = 0;
      let nowNumber = 0;
      let previousPrice = 0;
      let nowPrice = 0;
      previousItem.tempDishList.forEach(ele => {
        previousNumber += Number.parseFloat(ele.quantity);
        previousPrice +=
          Number.parseFloat(ele.quantity) * Number.parseFloat(ele.price);
      });
      item.tempDishList.forEach(ele => {
        nowNumber += Number.parseFloat(ele.quantity);
        nowPrice +=
          Number.parseFloat(ele.quantity) * Number.parseFloat(ele.price);
      });
      //console.log(previousNumber);
      //console.log(nowNumber);
      item.number = nowNumber;
      this.leftArr[item.parentsIndex].pluDtos[item.index] = item;
      this.leftArr[item.parentsIndex].number = (
        Number(this.leftArr[item.parentsIndex].number) +
        nowNumber -
        previousNumber
      ).toFixed(2);
      this.cartNumber = (
        Number(this.cartNumber) +
        nowNumber -
        previousNumber
      ).toFixed(2);
      this.totalPrice = (
        Number(this.totalPrice) +
        nowPrice -
        previousPrice
      ).toFixed(2);
    },
    clickLeftItemFun(item, index) {
      if (this.leftIndex == index) {
        return;
      }
      this.leftIndex = index;
      this.rightArr = this.leftArr[index].pluDtos;
      // console.log("this.rightArr:", this.rightArr);
      if (this.rightArr.length > 0) {
        this.$nextTick(() => {
          if (this.scrollRight) {
            this.scrollRight.refresh();
            this.scrollRight.scrollTo(0, 0);
          } else {
            this.scrollRight = new BScroll(this.$refs.wrapperRight, {
              click: true
            });
          }
        });
      } else {
        if (this.scrollRight) {
          this.scrollRight.destroy();
        }
      }
    },
    addFun(item, index) {
      let pluFlagIndex = item.pluFlag.indexOf("1") + 1;
      if (pluFlagIndex == 2) {
        this.selectedItem = item;
        this.showEditSeafoodCom = true;
      } else if (pluFlagIndex == 3) {
        this.selectedItem = item;
        this.showAddTempDish = true;
      } else {
        if (item.pluCondgplist.length > 0) {
          if (item.number == 0) {
            this.selectedItem = item;
            this.showEditPrivateTasteCom = true;
          } else {
            item.number = (Number(item.number) + 1).toFixed(2);
            item.number = Number.parseFloat(item.number);
            this.leftArr[item.parentsIndex].number = Number(
              Number(this.leftArr[item.parentsIndex].number) + 1
            ).toFixed(2);
            this.cartNumber = Number(Number(this.cartNumber) + 1).toFixed(2);
            this.totalPrice = (
              Number(this.totalPrice) +
              Number.parseFloat(item.pluPrice) +
              Number.parseFloat(item.dishSPrice)
            ).toFixed(2);
          }
        } else {
          item.number = (Number(item.number) + 1).toFixed(2);
          item.number = Number.parseFloat(item.number);
          this.leftArr[item.parentsIndex].number = Number(
            Number(this.leftArr[item.parentsIndex].number) + 1
          ).toFixed(2);
          this.cartNumber = Number(Number(this.cartNumber) + 1).toFixed(2);
          this.totalPrice = (
            Number(this.totalPrice) + Number.parseFloat(item.pluPrice)
          ).toFixed(2);
        }
      }
      //console.log("focusFun-item:", item.number);
      this.originalNumber = item.number;
    },
    cutBackFun(item, index) {
      let pluFlagIndex = item.pluFlag.indexOf("1") + 1;
      if (pluFlagIndex == 2) {
        if (item.number >= 1) {
          item.number = (item.number - 1).toFixed(2);
          item.number = Number.parseFloat(item.number);
          this.leftArr[item.parentsIndex].number = (
            Number(this.leftArr[item.parentsIndex].number) - 1
          ).toFixed(2);
          this.cartNumber = (Number(this.cartNumber) - 1).toFixed(2);
          this.totalPrice = (
            Number(this.totalPrice) -
            Number.parseFloat(item.pluPrice) +
            Number.parseFloat(item.dishSPrice)
          ).toFixed(2);
        } else {
          this.leftArr[item.parentsIndex].number = (
            Number(this.leftArr[item.parentsIndex].number) - item.number
          ).toFixed(2);
          this.cartNumber = (Number(this.cartNumber) - item.number).toFixed(2);
          this.totalPrice = (
            Number(this.totalPrice) -
            (Number.parseFloat(item.pluPrice) +
              Number.parseFloat(item.dishSPrice)) *
              item.number
          ).toFixed(2);
          item.number = 0;
        }
      } else if (pluFlagIndex == 3) {
        this.selectedItem = item;
        this.showTempDishList = true;
      } else {
        if (item.number >= 1) {
          if (item.pluCondgplist.length > 0) {
            this.totalPrice = (
              Number(this.totalPrice) -
              (Number.parseFloat(item.pluPrice) +
                Number.parseFloat(item.dishSPrice))
            ).toFixed(2);
          } else {
            this.totalPrice = (
              Number(this.totalPrice) - Number.parseFloat(item.pluPrice)
            ).toFixed(2);
          }

          item.number = (item.number - 1).toFixed(2);
          item.number = Number.parseFloat(item.number);
          this.leftArr[item.parentsIndex].number = (
            this.leftArr[item.parentsIndex].number - 1
          ).toFixed(2);
          this.cartNumber = (this.cartNumber - 1).toFixed(2);
        } else {
          if (item.pluCondgplist.length > 0) {
            this.totalPrice = (
              Number(this.totalPrice) -
              (Number.parseFloat(item.pluPrice) +
                Number.parseFloat(item.dishSPrice)) *
                item.number
            ).toFixed(2);
          } else {
            this.totalPrice = (
              Number(this.totalPrice) -
              Number.parseFloat(item.pluPrice) * item.number
            ).toFixed(2);
          }
          this.leftArr[item.parentsIndex].number = (
            this.leftArr[item.parentsIndex].number - item.number
          ).toFixed(2);
          this.cartNumber = (this.cartNumber - item.number).toFixed(2);
          item.number = 0;
        }
      }
      // console.log("focusFun-item:", item.number);
      this.originalNumber = item.number;
    },
    blurFun(item) {
      item.number = Number.parseFloat(item.number);
    },
    focusFun(item) {
      let pluFlagIndex = item.pluFlag.indexOf("1") + 1;
      if (pluFlagIndex == 3) {
        this.selectedItem = item;
        this.showAddTempDish = true;
      }
      // console.log("focusFun-item:", item.number);
      this.originalNumber = item.number;
    },
    inputFun(item, index) {
      console.log("inputFun-item:", item.number);

      let changeNumber = 0;
      if (!item.number) {
        item.number = 0;
      }
      item.number = item.number.toString();

      if (Number(item.number) > 100) {
        item.number = this.originalNumber;
        return;
      }

      item.number = item.number.replace(/[^\d.]/g, ""); //.
      item.number = item.number.replace(/\.{2,}/g, "."); //. 
      item.number = item.number
        .replace(".", "$#$")
        .replace(/\./g, "")
        .replace("$#$", ".");
      item.number = item.number.replace(/^(\-)*(\d+)\.(\d\d).*$/, "$1$2.$3"); //
      if (
        (item.number.indexOf(".") < 0 && item.number != "") ||
        item.number.indexOf(".") == 0
      ) {
        // 0102
        item.number = parseFloat(item.number).toString();
      }

      // console.log("inputFun-item:", item.number);
      //console.log("originalNumber:", this.originalNumber);
      // debugger;
      changeNumber = (
        Number(item.number) - Number(this.originalNumber)
      ).toFixed(2);
      changeNumber = Number(changeNumber);
      this.originalNumber = item.number;

      //console.log("inputFun-changeNumber:", changeNumber);
      // console.log("inputFun-type:", typeof changeNumber);
      let pluFlagIndex = item.pluFlag.indexOf("1") + 1;
      if (pluFlagIndex == 2) {
        this.leftArr[item.parentsIndex].number = (
          Number(this.leftArr[item.parentsIndex].number) + changeNumber
        ).toFixed(2);
        this.cartNumber = (Number(this.cartNumber) + changeNumber).toFixed(2);
        this.totalPrice =
          Number(this.totalPrice) +
          (Number.parseFloat(item.pluPrice) +
            Number.parseFloat(item.dishSPrice)) *
            changeNumber.toFixed(2);
      } else if (pluFlagIndex == 3) {
      } else {
        if (item.pluCondgplist.length > 0) {
          this.totalPrice = (
            Number(this.totalPrice) +
            (Number.parseFloat(item.pluPrice) +
              Number.parseFloat(item.dishSPrice)) *
              changeNumber
          ).toFixed(2);
        } else {
          this.totalPrice =
            Number(this.totalPrice) +
            Number.parseFloat(item.pluPrice) * changeNumber.toFixed(2);
        }
        this.leftArr[item.parentsIndex].number = (
          Number(this.leftArr[item.parentsIndex].number) + changeNumber
        ).toFixed(2);
        this.cartNumber = (Number(this.cartNumber) + changeNumber).toFixed(2);
      }
    },
    gotoCartFun() {
      if (this.cartNumber > 0) {
        // console.log("shoppingCart:", shoppingCart);
        let shoppingCart = [];
        this.leftArr.forEach(ele => {
          if (ele.number > 0) {
            ele.pluDtos.forEach(opt => {
              if (opt.number > 0) {
                shoppingCart.push(opt);
              }
            });
          }
        });
        this.$store.commit("sumbitShoppingCart", shoppingCart);
        this.$router.push("/mobileOrdering/shoppingCart");
      } else {
        this.msg("");
      }
    },
    async okFun() {
      if (this.cartNumber > 0) {
        let shoppingCart = [];
        let postData = [];
        this.leftArr.forEach(ele => {
          if (ele.number > 0) {
            ele.pluDtos.forEach(opt => {
              if (opt.number > 0) {
                shoppingCart.push(opt);
              }
            });
          }
        });
        let dcgateway = localStorage.getItem("dcgateway")
          ? JSON.parse(localStorage.getItem("dcgateway"))
          : {};
        let useName = dcgateway.userInfo.userName;
        console.log("shoppingCart:", shoppingCart);
        shoppingCart.forEach(ele => {
          let dishItem = {};
          let pluFlagIndex = ele.pluFlag.indexOf("1");
          let dishItemUpper = {
            pluState: 1,
            pccode: ele.pccode,
            sortCode: ele.sortCode,
            descript: ele.pluDescript,
            descriptEn: ele.pluDescriptEn,
            sortCode: ele.sortCode,
            pluCode: ele.pluCode,
            tocode: ele.pluTocode,
            noteCode: ele.noteCode,
            flag: ele.pluFlag,
            sta: "I",
            empid: useName,
            tableno: this.selectedTableInfo.tableNo,
            inumber: 0
          };
          let dishItemLower = {};

          if (pluFlagIndex == 0) {
            //1--
            let cook = "";
            let condCode = "";
            let dishSPrice = Number.parseFloat(ele.pluPrice);
            dishItemLower = {
              number: ele.number,
              cook: "",
              condCode: "",
              info: ele.remarks || "",
              unit: ele.pluUnit,
              price: ele.pluPrice,
              amount: dishSPrice * ele.number,
              pluStds: ele.pluStds
            };
            dishItem = { ...dishItemUpper, ...dishItemLower };
            postData.push(dishItem);
          } else if (pluFlagIndex == 1) {
            //2
            let cook = "";
            let condCode = "";
            let cookList = [];
            let dishSPrice = Number.parseFloat(ele.pluPrice);
            let tastePrice = 0;
            ele.posCondstDtoList.forEach(element => {
              element.posConddtlList.forEach(opt => {
                if (opt.selected) {
                  if (opt.price > 0) {
                    cook += `${opt.descript}(${opt.price}),`;
                    let cookObj = {
                      code: opt.code,
                      descript: opt.descript,
                      descriptEn: "",
                      price: opt.price,
                      unit: "",
                      number: ele.number
                    };
                    cookList.push(cookObj);
                  } else {
                    cook += opt.descript + ",";
                  }

                  condCode += opt.code + ",";
                  dishSPrice += Number.parseFloat(opt.price);
                  tastePrice += Number.parseFloat(opt.price);
                }
              });
            });
            dishItemLower = {
              number: ele.number,
              cook: cook,
              condCode: condCode,
              info: ele.remarks || "",
              unit: ele.pluUnit,
              price: ele.pluPrice,
              amount: dishSPrice * ele.number,
              tastePrice: tastePrice,
              cookList: cookList
            };
            dishItem = { ...dishItemUpper, ...dishItemLower };
            postData.push(dishItem);
          } else if (pluFlagIndex == 2) {
            //
            //dishItemLower;
            ele.tempDishList.forEach(element => {
              dishItemLower = {
                descript: element.name,
                descriptEn: element.name,
                number: element.quantity,
                cook: "",
                condCode: "",
                info: "",
                unit: element.unit,
                price: element.price,
                amount: element.price * element.quantity
              };
              dishItem = { ...dishItemUpper, ...dishItemLower };
              postData.push(dishItem);
            });
          } else {
            if (ele.pluCondgplist.length > 0) {
              //
              let cook = "";
              let condCode = "";
              let cookList = [];
              let dishSPrice = Number.parseFloat(ele.pluPrice);
              let tastePrice = 0;
              ele.pluCondgplist.forEach(opt => {
                if (opt.selected) {
                  if (opt.price > 0) {
                    cook += `${opt.descript}(${opt.price}),`;
                    let cookObj = {
                      code: opt.code,
                      descript: opt.descript,
                      descriptEn: "",
                      price: opt.price,
                      unit: "",
                      number: ele.number
                    };
                    cookList.push(cookObj);
                  } else {
                    cook += opt.descript + ",";
                  }
                  condCode += opt.code + ",";
                  dishSPrice += Number.parseFloat(opt.price);
                  tastePrice += Number.parseFloat(opt.price);
                }
              });
              dishItemLower = {
                number: ele.number,
                cook: cook,
                condCode: condCode,
                info: ele.remarks || "",
                unit: ele.pluUnit,
                price: ele.pluPrice,
                amount: dishSPrice * ele.number,
                tasteType: "private",
                tastePrice: tastePrice * ele.number,
                cookList: cookList
              };
              dishItem = { ...dishItemUpper, ...dishItemLower };
              postData.push(dishItem);
            } else {
              //
              let cook = "";
              let condCode = "";
              let dishSPrice = Number.parseFloat(ele.pluPrice);
              dishItemLower = {
                number: ele.number,
                cook: "",
                condCode: "",
                info: ele.remarks || "",
                unit: ele.pluUnit,
                price: ele.pluPrice,
                amount: dishSPrice * ele.number
              };
              dishItem = { ...dishItemUpper, ...dishItemLower };
              postData.push(dishItem);
            }
          }
        });

        //console.log("shoppingCart:", shoppingCart);
        if (this.selectedTableInfo.accnt) {
          // let data = {
          //   params: {
          //     accnt: this.selectedTableInfo.accnt,
          //     orderList: JSON.stringify(postData)
          //   }
          // };
          let data = {
            accnt: this.selectedTableInfo.accnt,
            orderList: JSON.stringify(postData),
            method: "saveCacheOrder"
          };
          console.log(data);
          let res = await ajaxPost("/pos/router", data);
          if (res && res.resultCode == 0) {
            this.$router.push("/mobileOrdering/myOrder");
          }
        } else {
          console.log("postData:", postData);
          this.$store.commit("submitTempTable", postData);
          this.$router.push("/mobileOrdering/myOrder");
        }
      } else {
        this.msg("");
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  },
  filters: {
    toFixed(val) {
      // console.log("toFixed-val:", val);
      // console.log("toFixed-val-type:", typeof val);
      // return val ? Number.parseFloat(val.toFixed(2)).toFixed(2) : 0;
      return val ? Number(val).toFixed(2) : 0;
    },
    deleteZero(val) {
      return Number.parseFloat(val);
    }
  }
};
</script>
<style lang="less" scoped>
.container-orderDish {
  .content-header {
    background-color: #393c41;
    height: 45px;
    width: 100%;
    display: flex;
    align-items: center;
    padding-top: 7px;
    padding-left: 15px;
    padding-right: 12px;
    box-sizing: border-box;
    .content-goback {
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 10px;
      .goback-text {
        font-size: 16px;
      }
    }
  }

  .container-main {
    .dis-container {
      background-color: #f6f8f9;
      position: absolute;
      width: 100%;
      top: 46px;
      bottom: 60px;
      overflow: hidden;
      display: flex;
      justify-content: space-between;
      .left {
        width: 100px;
        .left-item {
          position: relative;
          height: 55px;
          text-align: left;
          font-size: 14px;
          color: #333333;
          display: flex;
          align-items: center;
          .text {
            display: inline-block;
            // max-width: 60px;
            padding-left: 13px;
            padding-right: 13px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          .tips {
            display: inline-block;
            position: relative;
            top: -10px;
            left: -15px;
            // width: 18px;
            // height: 18px;
            padding: 0 5px;
            line-height: 18px;
            text-align: center;
            background-color: #d73c3c;
            border-radius: 55px;
            color: #fff;
            transform: scale(0.8);
          }
        }
        .left-item-active {
          background-color: #ffffff;
          color: #c4ad68;
        }
      }
      .right {
        background-color: #fff;
        flex: 1;
        .right-item {
          padding-left: 18px;
          padding-right: 15px;
          height: 55px;
          font-size: 14px;
          color: #666666;
          border-bottom: 1px solid #ddd;
          background-color: #fff;
          text-align: left;
          font-size: 14px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          .dish-name {
            max-width: 140px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            color: #000000;
          }
          .dish-price {
            font-size: 12px;
            color: #666666;
          }
          .quantity-content {
            display: flex;
            align-items: center;
            .iconfont {
              font-size: 30px;
              color: #c4ad68;
            }
            .quantity {
              color: #000000;
              width: 50px;
              text-align: center;
              border: none;
              outline: none;
            }
          }
        }
        .right-item-active {
          color: #c4ad68;
          .iconfont {
            display: block;
            color: #c4ad68;
          }
        }
      }
    }

    .no-list {
      margin: 150px auto;
    }
  }
  .container-footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 60px;
    background: #393c41;
    display: flex;
    align-items: center;
    .shoppingCart-content {
      position: relative;
      bottom: 3px;
      left: 10px;
      width: 80px;
      height: 80px;
      border-radius: 100px;
      background-color: #393c41;
      .iconfont {
        margin-top: 3px;
        color: #c4ad68;
        font-size: 46px;
      }
      .quantity {
        position: absolute;
        top: 18px;
        left: 50px;
        // width: 15px;
        // height: 15px;
        padding: 0 5px;
        line-height: 15px;
        border-radius: 100px;
        color: #fff;
        background-color: #d73c3c;
      }
    }
    .totalPrice-content {
      margin-left: 10px;
      font-size: 24px;
      color: #ffffff;
      .unit {
        font-size: 14px;
      }
    }
    .ok-btn {
      position: absolute;
      right: 15px;
      top: 50%;
      transform: translateY(-50%);
      font-size: 18px;
      color: #c4ad68;
    }
  }
}
</style>



