<template>
  <div class="temporaryDishModify">
    <headerUsually
      btnShow="true"
      titleText=""
      url
      :isComponents="isComponents"
      @emitGoback="gobackFun()"
    ></headerUsually>
    <div class="content">
      <div class="item name">
        <div class="left"></div>
        <div class="right">
          <input type="text" v-model="name" placeholder="" :maxlength="maxlength.length10" />
        </div>
      </div>
      <div class="item quantity">
        <div class="left"></div>
        <div class="right">
          <i class="iconfont icon-jian" @click="cutBackFun()"></i>
          <input class="num" type="text" v-model="quantity" />
          <!-- <span class="num">{{quantity}}</span> -->
          <i class="iconfont icon-jia2" @click="addFun()"></i>
        </div>
      </div>
      <div class="item amount">
        <div class="left"></div>
        <div class="right">
          <input type="number" v-model="price" placeholder="" />
        </div>
      </div>
      <div class="item unit">
        <div class="left"></div>
        <div class="right">
          <input type="text" v-model="unit" placeholder="" :maxlength="maxlength.length4" />
        </div>
      </div>
    </div>
    <div class="btn" @click="okFun()"></div>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
export default {
  components: {
    headerUsually
  },
  props: {
    selectedItem: Object,
    selectedIndex: {
      type: Number,
      default: -1
    }
  },
  data() {
    return {
      isComponents: true,
      receiveItem: {},
      name: "",
      quantity: "1",
      price: "",
      unit: "",
      maxlength: {
        length10: 10,
        length4: 4
      },
      originalNumber: 0
    };
  },
  created() {
    //console.log("selectedItem:", this.selectedItem);
    this.receiveItem = _.cloneDeep(this.selectedItem);
    if (this.receiveItem.tempDishList) {
      if (this.selectedIndex > -1) {
        let item = this.receiveItem.tempDishList[this.selectedIndex];
        this.name = item.name;
        this.quantity = item.quantity;
        this.price = item.price;
        this.unit = item.unit;
      }
    } else {
      this.receiveItem.tempDishList = [];
    }
  },
  methods: {
    gobackFun() {
      this.$emit("emitHiddenAddTempDish");
    },
    cutBackFun() {
      if (this.selectedIndex > -1) {
        if (this.quantity > 0) {
          this.quantity--;
        }
      } else {
        if (this.quantity > 1) {
          this.quantity--;
        }
      }
    },
    addFun() {
      this.quantity++;
    },
    okFun() {
      if (!this.name || !this.price || !this.unit) {
        this.msg("");
      } else {
        let dcgateway = localStorage.getItem("dcgateway")
          ? JSON.parse(localStorage.getItem("dcgateway"))
          : {};
        let tempSDish = {
          name: this.name,
          quantity: this.quantity,
          price: Number.parseFloat(this.price).toFixed(2),
          unit: this.unit,
          waiter: dcgateway.userInfo.userName
        };
        if (this.selectedIndex > -1) {
          this.receiveItem.tempDishList[this.selectedIndex] = tempSDish;
        } else {
          this.receiveItem.tempDishList.push(tempSDish);
        }
        this.$emit("emitHiddenAddTempDish", this.receiveItem);
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  },
  watch: {
    quantity() {
      //console.log("val:", val);
      this.quantity = this.quantity.toString();

      if (Number(this.quantity) > 100) {
        this.quantity = this.originalNumber;
        return;
      }

      this.quantity = this.quantity.replace(/[^\d.]/g, ""); //.
      this.quantity = this.quantity.replace(/\.{2,}/g, "."); //. 
      this.quantity = this.quantity
        .replace(".", "$#$")
        .replace(/\./g, "")
        .replace("$#$", ".");
      this.quantity = this.quantity.replace(
        /^(\-)*(\d+)\.(\d\d).*$/,
        "$1$2.$3"
      ); //
      if (this.quantity.indexOf(".") < 0 && this.quantity != "") {
        // 0102
        this.quantity = parseFloat(this.quantity).toString();
      }
      this.originalNumber = this.quantity;
    }
  }
};
</script>
<style lang="less" scoped>
.temporaryDishModify {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9;
  top: 46px;
  background-color: #f6f8f9;
  .content {
    padding-left: 18px;
    background-color: #fff;

    .item {
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #ddd;
      padding-right: 17px;
      .left {
        font-size: 14px;
        color: #666666;
      }
      .right {
        display: flex;
        align-items: center;
        font-size: 14px;
        color: #333333;
        input {
          text-align: right;
          outline: none;
          border: none;
        }
        .iconfont {
          color: #a8acb8;
        }
      }
    }
    .item:last-child {
      border-bottom: none;
    }
    .quantity {
      .right {
        .iconfont {
          color: #c4ad68;
          font-size: 30px;
        }
        .num {
          // margin: 0 20px;
          width: 50px;
          text-align: center;
          border: none;
          outline: none;
        }
      }
    }
  }
  .btn {
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
}
</style>



