<template>
  <div class="container-seafoodEditor">
    <headerUsually
      btnShow="true"
      titleText=""
      url
      :isComponents="isComponents"
      @emitGoback="gobackFun()"
    ></headerUsually>
    <div class="content-editor">
      <div class="item quantity">
        <div class="left"></div>
        <div class="right">
          <input type="number" v-model="receiveItem.number" @input="inputFun(receiveItem.number)" />
        </div>
      </div>
      <div class="item remarks">
        <div class="left"></div>
        <div class="right">
          <input type="text" v-model="receiveItem.remarks" />
        </div>
      </div>
    </div>
    <!-- v-if="receiveItem.posCondstDtoList.length>0" -->
    <div class="content-taste">
      <div class="left" ref="wrapperLeft">
        <ul class="container">
          <!-- <li class="left-item">------- ------</li>
          <li class="left-item left-item-active">------- --</li>-->
          <li
            class="left-item"
            :class="leftIndex==index?'left-item-active':''"
            v-for="(item, index) in receiveItem.posCondstDtoList"
            :key="index"
            @click="leftClickFun(item,index)"
          >{{item.descript}}</li>
        </ul>
      </div>
      <div class="right" ref="wrapperRight">
        <ul class="container">
          <li
            class="right-item"
            :class="item.selected?'right-item-active':''"
            @click="rightClickFun(item,index)"
            v-for="(item, index) in rightArr"
            :key="index"
          >
            <span class="text" v-if="item.price>0">{{item.descript}} (+{{item.price}})</span>
            <span class="text" v-else>{{item.descript}}</span>
            <span class="iconfont icon-xuanzhong" v-if="item.selected"></span>
          </li>
          <!-- <li class="right-item right-item-active">
            <span class="text">11111</span>
            <span class="iconfont icon-xuanzhong"></span>
          </li>-->
        </ul>
      </div>
    </div>
    <div class="btn" @click="okFun()"></div>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import BScroll from "better-scroll";
export default {
  components: {
    headerUsually
  },
  props: {
    selectedItem: Object
  },
  data() {
    return {
      isComponents: true,
      leftIndex: 0,
      rightArr: [],
      scrollRight: {},
      receiveItem: {}
    };
  },
  created() {
    console.log("selectedItem:", this.selectedItem);
    this.receiveItem = _.cloneDeep(this.selectedItem);
    if (this.receiveItem.number == 0) {
      this.receiveItem.posCondstDtoList.forEach(ele => {
        ele.posConddtlList.forEach(opt => {
          this.$set(opt, "selected", false);
        });
      });
    }
    this.receiveItem.remarks = "";
    this.receiveItem.dishSPrice = 0;
    this.receiveItem.number = "";

    if (this.receiveItem.posCondstDtoList.length > 0) {
      this.rightArr = this.receiveItem.posCondstDtoList[0].posConddtlList;
    }
  },
  mounted() {
    this.$nextTick(() => {
      let scrollLeft = new BScroll(this.$refs.wrapperLeft, { click: true });
      this.scrollRight = new BScroll(this.$refs.wrapperRight, { click: true });
    });
  },
  methods: {
    gobackFun() {
      this.$emit("emitHiddenEditSeafood");
    },
    leftClickFun(item, index) {
      if (this.leftIndex == index) {
        return;
      }
      this.leftIndex = index;
      this.rightArr = this.receiveItem.posCondstDtoList[index].posConddtlList;
      this.$nextTick(() => {
        this.scrollRight.refresh();
        this.scrollRight.scrollTo(0, 0);
      });
    },
    rightClickFun(item, index) {
      //console.log(item);
      item.selected = !item.selected;
      // if (item.selected) {
      //   item.selected = false;
      // } else {
      //   item.selected = true;
      // }
    },
    inputFun(val) {
      if (!val) {
        val = 0;
      }
      val = val.toString();
      val = val.replace(/[^\d.]/g, ""); //.
      val = val.replace(/\.{2,}/g, "."); //. 
      val = val
        .replace(".", "$#$")
        .replace(/\./g, "")
        .replace("$#$", ".");
      val = val.replace(/^(\-)*(\d+)\.(\d\d).*$/, "$1$2.$3"); //
      if (val.indexOf(".") < 0 && val != "") {
        // 0102
        val = parseFloat(val).toString();
      }
      this.receiveItem.number = val;
    },
    okFun() {
      //       this.receiveItem.number > 0 &&
      //
      //^[\+\-]?\d+(\.\d+)?$
      if (
        this.receiveItem.number > 0 &&
        this.receiveItem.number.indexOf("e") == -1
      ) {
        console.log("receiveItem:", this.receiveItem);
        //this.receiveItem.dishPrice =
        let dishSPrice = 0;
        this.receiveItem.posCondstDtoList.forEach(element => {
          element.posConddtlList.forEach(ele => {
            if (ele.selected) {
              dishSPrice += Number.parseFloat(ele.price);
            }
          });
        });
        this.receiveItem.dishSPrice = dishSPrice;
        this.$emit("emitHiddenEditSeafood", this.receiveItem);
      } else {
        this.msg("0");
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.container-seafoodEditor {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9;
  top: 46px;
  background-color: #f6f8f9;
  .content-editor {
    padding-left: 18px;
    background-color: #fff;
    .item {
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #ddd;
      padding-right: 17px;
      .left {
        font-size: 14px;
        color: #666666;
      }
      .right {
        width: 70%;
        display: flex;
        align-items: center;
        font-size: 14px;
        color: #333333;
        input {
          text-align: right;
          outline: none;
          border: none;
          width: 100%;
        }
        .iconfont {
          color: #a8acb8;
        }
      }
    }
    .item:last-child {
      border-bottom: none;
    }
    .quantity {
      .right {
        .iconfont {
          color: #c4ad68;
          font-size: 30px;
        }
        .num {
          margin: 0 20px;
        }
      }
    }
  }
  .content-taste {
    background-color: #f6f8f9;
    position: absolute;
    width: 100%;
    top: 110px;
    bottom: 50px;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    .left {
      width: 140px;
      .left-item {
        height: 55px;
        line-height: 55px;
        text-align: left;
        font-size: 14px;
        color: #333333;
        padding-left: 13px;
        padding-right: 13px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .left-item-active {
        background-color: #ffffff;
      }
    }
    .right {
      background-color: #fff;
      flex: 1;
      .right-item {
        padding-left: 18px;
        padding-right: 15px;
        height: 55px;
        line-height: 55px;
        font-size: 14px;
        color: #666666;
        border-bottom: 1px solid #ddd;
        display: flex;
        align-content: center;
        justify-content: space-between;
        background-color: #fff;
        font-size: 14px;
      }
      .right-item-active {
        color: #c4ad68;
        .iconfont {
          display: block;
          color: #c4ad68;
        }
      }
    }
  }
  .btn {
    position: fixed;
    bottom: 0;
    width: 100%;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 12px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.5);
  }
}
</style>



